<template>
  <div id="container">
    <HeaderUserBar />

    <div id="partners">
      <div class="partners-title">Партнеры</div>
      <div class="partners-grid">
<!--        <div class="partners-card">-->
<!--          <img src="../files/depomall.webp" alt="Partner #1">-->
<!--        </div>-->
        <div class="desc" v-if="lang === 'ru'">Спонсоры: ряд партнёров и спонсоров запустится 1 декабря 2022г.</div>
        <div class="desc" v-else>Homiylar: Bir qator hamkorlar va homiylar 2022-yil 1-dekabrda ishga tushadi.</div>
      </div>
    </div>

    <BottomNavigationMenu active="partners" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";
export default {
  name: "Partners",
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  data: () => ({
    lang: null,
  }),
  async mounted() {
    await axios.get('https://server.gold-ticket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          this.lang = response.data.data.lang
        })
        .catch(error => console.log(error));
  }
}
</script>

